$mainTitle: #555555;

div.container {
  h4 {
    color: $mainTitle;
  }
}

@mixin 함수() {
  background: #eeeeee;
  padding: 20px;
  border-radius: 1rem;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

.my-alert-yellow {
  @extend .my-alert; /**my alert라는 태그의 css를 복붙한거랑 같은 원리*/
  background: #ffe591;
}

.wow-enter {
  /**애니메이션 시작할때 적용*/
  opacity: 0;
}

.wow-enter-active {
  /**애니메이션 동작할때 적용*/
  opacity: 1;
  transition: all 500ms;
}

.tab1 {
  background: palegoldenrod;
  padding: 2.5rem;
}
.tab2 {
  background: powderblue;
  padding: 2.5rem;
}
